.logo {
  display: flex;
  align-items: center;
  margin: auto;
  position: relative;
  width: 100px;
  height: 100px;
  -webkit-filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 1));
          filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 1));
  transition: transform 1s;
}
.logo:hover {
  transform: rotateZ(360deg) scale(0.8, 0.8);
  transition: transform 1s;
}
.header ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-filter: var(--shadow-header-dark);
          filter: var(--shadow-header-dark);
  align-items: center;
}
.header span {
  padding: 10px;
}
.hlink {
  position: relative;
  padding-top: 5px;
  padding-bottom: 5px;
}
.hlink::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--text-color-dark);
  position: absolute;
  left: 0;
  bottom: 0px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s ease;
}
.hlink:hover::after {
  transform: scaleX(1);
}

